.history {
  width: 874px;
  margin: 0 auto;
  border-collapse: collapse;
  border-radius: 3px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.25);
}

.trStyle {
  padding: 0px;
  background: #ebeef2;
  text-align: center;
}

th,
td {
  padding: 0px;
  border-bottom: 2px solid rgb(235, 238, 242);
  height: 68px;
  text-align: center;
}
